.body1,.work_card>p,.how_we_work .left_side p{font-size:1rem;font-weight:400;line-height:1.75rem}.body2{font-size:1rem;font-weight:500;line-height:1.75rem}.label1,.card_icon>.icon_text,.how_we_work .left_side a{font-size:1.125rem;font-weight:500;line-height:2rem}.label2{font-size:1rem;font-weight:500;line-height:1.75rem}.btn{padding:1em 3em;cursor:pointer;white-space:nowrap;text-decoration:none;border:unset;border-radius:2.625em;transition:background-color .3s ease-in-out}.btn.yellow{background-color:#fcd980;color:#1b1c2b}.btn.yellow:hover{background-color:#fde4a6}.btn.dark_blue{color:#fff;background-color:#282938}.btn.dark_blue:hover{background-color:#535460}.arrow_link::after,.how_we_work .left_side a::after{position:absolute;margin-left:.5em;content:"→";transition:transform .3s ease-in-out}.arrow_link:hover::after,.how_we_work .left_side a:hover::after{transform:translateX(0.5em)}.how_we_work{display:flex;justify-content:space-between;padding:6rem 10rem;color:#282938;background-color:#f4f6fc}.how_we_work .left_side p{display:block;width:25em;margin-top:1em;opacity:.7}.how_we_work .left_side a{display:block;margin-top:1em;color:#2405f2}.how_we_work .cards_wrapper{display:grid;max-width:45%;grid-template-columns:1fr 1fr;grid-template-rows:1fr;grid-gap:2.5em 1.5em}.work_card>h5{margin-top:.5em}.work_card>p{margin-top:.5em;opacity:.7}.card_icon{position:relative;overflow:hidden;width:3.125em;height:3.125em;border-radius:.5em;background-color:#2405f2}.card_icon>.icon_part1{position:absolute;top:1.5em;right:1.5em;width:3.125em;height:3.125em;border-radius:.5em;background-color:#fcd980}.card_icon>.icon_part2{position:absolute;top:-0.55em;right:-0.55em;width:3.125em;height:3.125em;border-radius:.5em;background-color:#5239fa}.card_icon>.icon_text{position:absolute;top:50%;left:50%;transform:translate(-50%, -50%);color:#fff}