.body1,.reviews .left_side p{font-size:1rem;font-weight:400;line-height:1.75rem}.body2{font-size:1rem;font-weight:500;line-height:1.75rem}.label1,.reviews .right_side .review .author .author_wrapper .author_name{font-size:1.125rem;font-weight:500;line-height:2rem}.label2{font-size:1rem;font-weight:500;line-height:1.75rem}.btn{padding:1em 3em;cursor:pointer;white-space:nowrap;text-decoration:none;border:unset;border-radius:2.625em;transition:background-color .3s ease-in-out}.btn.yellow{background-color:#fcd980;color:#1b1c2b}.btn.yellow:hover{background-color:#fde4a6}.btn.dark_blue{color:#fff;background-color:#282938}.btn.dark_blue:hover{background-color:#535460}.arrow_link::after{position:absolute;margin-left:.5em;content:"→";transition:transform .3s ease-in-out}.arrow_link:hover::after{transform:translateX(0.5em)}.reviews{display:flex;justify-content:space-between;padding:6rem 10rem;background-color:rgba(244,246,252,.5)}.reviews .left_side{width:20em}.reviews .left_side p{margin-top:1em;opacity:.7}.reviews .right_side{position:relative;display:flex;overflow:hidden;width:45em;height:15em;margin-left:6.8em}.reviews .right_side .review{position:absolute;display:flex;flex-direction:column;justify-content:space-between;width:100%;height:100%;transition:left .5s ease,opacity .3s ease}.reviews .right_side .review.left_review{left:-100%;opacity:0}.reviews .right_side .review.active_review{left:0}.reviews .right_side .review.right_review{left:100%;opacity:0}.reviews .right_side .review .author{display:flex;margin-top:3em}.reviews .right_side .review .author img{width:3em;height:3em;border-radius:100%}.reviews .right_side .review .author .author_wrapper{margin-left:1em}.reviews .right_side .review .author .author_wrapper :not(:first-child){font-size:.75rem}.reviews .right_side .controls{position:absolute;right:0;bottom:0;display:flex}.reviews .right_side .controls button{display:flex;align-items:center;justify-content:center;width:3em;height:3em;padding:0;cursor:pointer;border:none;border-radius:100%}.reviews .right_side .controls button svg{font-size:1.375rem;display:inline-block;transition:transform .3s ease}.reviews .right_side .controls button:hover svg{transform:scale(1.2)}.reviews .right_side .controls button.left_arrow{opacity:.6;background:rgba(0,0,0,0)}.reviews .right_side .controls button.right_arrow{margin-left:.5em;background-color:#5239fa}.reviews .right_side .controls button.right_arrow svg{color:#fff}