.body1,.card p{font-size:1rem;font-weight:400;line-height:1.75rem}.body2{font-size:1rem;font-weight:500;line-height:1.75rem}.label1{font-size:1.125rem;font-weight:500;line-height:2rem}.label2,.features p{font-size:1rem;font-weight:500;line-height:1.75rem}.btn{padding:1em 3em;cursor:pointer;white-space:nowrap;text-decoration:none;border:unset;border-radius:2.625em;transition:background-color .3s ease-in-out}.btn.yellow{background-color:#fcd980;color:#1b1c2b}.btn.yellow:hover{background-color:#fde4a6}.btn.dark_blue{color:#fff;background-color:#282938}.btn.dark_blue:hover{background-color:#535460}.arrow_link::after{position:absolute;margin-left:.5em;content:"→";transition:transform .3s ease-in-out}.arrow_link:hover::after{transform:translateX(0.5em)}.features{padding:6rem 10rem;text-align:center;color:#282938;background-color:#f4f6fc}.features>h2{display:inline-block;width:15em;margin-top:.75em}.features .cards_wrapper{display:grid;margin-top:3em;grid-template-columns:repeat(3, 1fr);grid-template-rows:repeat(2, 1fr);grid-column-gap:2em;grid-row-gap:1.5em}.features .cards_wrapper .card:nth-child(1){grid-area:1/1/2/2}.features .cards_wrapper .card:nth-child(2){grid-area:1/2/2/3}.features .cards_wrapper .card:nth-child(3){grid-area:1/3/2/4}.features .cards_wrapper .card:nth-child(4){grid-area:2/1/3/2}.features .cards_wrapper .card:nth-child(5){grid-area:2/2/3/3}.features .cards_wrapper .card:nth-child(6){grid-area:2/3/3/4}.features .cards_wrapper .card:nth-child(7){grid-area:3/1/4/2}.features .cards_wrapper .card:nth-child(8){grid-area:3/2/4/3}.features .cards_wrapper .card:nth-child(9){grid-area:3/3/4/4}.card{max-width:25em;padding:3em;text-align:left;background-color:#fff}.card img{width:2em;height:2em;object-fit:contain}.card h6{margin-top:1.5em}.card p{margin-top:.75em;opacity:.7}