.body1{font-size:1rem;font-weight:400;line-height:1.75rem}.body2{font-size:1rem;font-weight:500;line-height:1.75rem}.label1,.faq .right_side .question .question_wrapper .answer_wrapper .answer{font-size:1.125rem;font-weight:500;line-height:2rem}.label2{font-size:1rem;font-weight:500;line-height:1.75rem}.btn{padding:1em 3em;cursor:pointer;white-space:nowrap;text-decoration:none;border:unset;border-radius:2.625em;transition:background-color .3s ease-in-out}.btn.yellow{background-color:#fcd980;color:#1b1c2b}.btn.yellow:hover{background-color:#fde4a6}.btn.dark_blue{color:#fff;background-color:#282938}.btn.dark_blue:hover{background-color:#535460}.arrow_link::after{position:absolute;margin-left:.5em;content:"→";transition:transform .3s ease-in-out}.arrow_link:hover::after{transform:translateX(0.5em)}.faq{display:flex;justify-content:space-between;padding:6rem 10rem;color:#282938}.faq .left_side{width:20em}.faq .left_side a{display:block;margin-top:1em;color:#2405f2}.faq .left_side a:hover{text-decoration:underline}.faq .right_side .question.open_answer .open_sign svg{transform:rotate(135deg)}.faq .right_side .question.open_answer .question_wrapper{overflow:clip;height:7.5em}.faq .right_side .question hr{margin:0}.faq .right_side .question .question_wrapper{display:flex;overflow:hidden;width:45em;height:2em;padding:2em 0 2em 2em;transition:height .3s ease}.faq .right_side .question .question_wrapper>h6{color:#2405f2}.faq .right_side .question .question_wrapper .answer_wrapper{position:relative;margin-left:3.5em}.faq .right_side .question .question_wrapper .answer_wrapper .answer{display:block;margin-top:1.2em;opacity:.87}.faq .right_side .question .question_wrapper .answer_wrapper .open_sign{position:absolute;top:.5em;right:0;cursor:pointer;opacity:.7}.faq .right_side .question .question_wrapper .answer_wrapper .open_sign svg{font-size:1rem;transition:transform .3s ease}