.body1,.blog main .post .desc{font-size:1rem;font-weight:400;line-height:1.75rem}.body2{font-size:1rem;font-weight:500;line-height:1.75rem}.label1{font-size:1.125rem;font-weight:500;line-height:2rem}.label2,.blog main .post a,.blog main .post .date{font-size:1rem;font-weight:500;line-height:1.75rem}.btn{padding:1em 3em;cursor:pointer;white-space:nowrap;text-decoration:none;border:unset;border-radius:2.625em;transition:background-color .3s ease-in-out}.btn.yellow{background-color:#fcd980;color:#1b1c2b}.btn.yellow:hover{background-color:#fde4a6}.btn.dark_blue{color:#fff;background-color:#282938}.btn.dark_blue:hover{background-color:#535460}.arrow_link::after,.blog main .post a::after{position:absolute;margin-left:.5em;content:"→";transition:transform .3s ease-in-out}.arrow_link:hover::after,.blog main .post a:hover::after{transform:translateX(0.5em)}.blog{padding:6rem 10rem;color:#282938}.blog main{display:flex;justify-content:space-between;margin-top:4em;gap:2em}.blog main .post img{width:100%;max-width:24em;height:17em;object-fit:cover}.blog main .post .date{margin-top:2.5em;opacity:.7}.blog main .post h6{margin-top:.5em}.blog main .post .desc{margin-top:1em;opacity:.7}.blog main .post a{display:block;margin-top:1.5em}